import React, { Component } from 'react';
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Alert
} from 'reactstrap';
import { connect } from 'react-redux';
import {Redirect} from 'react-router-dom';
import PropTypes from 'prop-types';
import FileUpload from '../FileUpload/FileUpload'
import { login } from '../../actions/authActions';
import { clearErrors } from '../../actions/errorActions';

class LoginModal extends Component {
  state = {
    modal: false,
    email: '',
    password: '',
    msg: null
  };

  static propTypes = {
    isAuthenticated: PropTypes.bool,
    error: PropTypes.object.isRequired,
    login: PropTypes.func.isRequired,
    clearErrors: PropTypes.func.isRequired
  };

  componentDidUpdate(prevProps) {
    const { error, isAuthenticated } = this.props;
    if (error !== prevProps.error) {
      // Check for register error
      if (error.id === 'LOGIN_FAIL') {
        this.setState({ msg: error.msg.msg });
      } else {
        this.setState({ msg: null });
      }
    }
    console.log("fdfd")
    // If authenticated, close modal
      if (isAuthenticated) {
        this.props.history.push('/userType')
    }
  }

  toggle = () => {
    // Clear errors
    this.props.clearErrors();
    this.setState({
      modal: !this.state.modal
    });
  };

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();

    const { email, password } = this.state;

    const user = {
      email,
      password
    };

    // Attempt to login
    console.log("BEFORE");
    this.props.login(user);

    // const { isAuthenticated } = this.props;
    // if(isAuthenticated==true){
    //     console.log("SSS")
    // }
  };

  render() {
    return (
      <div>
            {this.state.msg ? (
              <Alert color='danger'>{this.state.msg}</Alert>
            ) : null}
            <Form onSubmit={this.onSubmit}>
              <FormGroup style={{display:"grid",justifyContent:"center", flexDirection:"column",paddingTop:"100px"}}>
              <h2 style={{color:"#ff6600",textAlign:"center"}}>Connextion</h2>
              <br />
                <Input
                  type='email'
                  name='email'
                  id='email'
                  style={{width:350, borderColor:'#ff6600'}}
                  placeholder='Email'
                  className='mb-3'
                  onChange={this.onChange}
                />

                <br />
                <Input
                  type='password'
                  name='password'
                  id='password'
                  style={{width:350, borderColor:'#ff6600'}}
                  placeholder='Password'
                  className='mb-3'
                  onChange={this.onChange}
                />
                <Button style={{ marginTop: '2rem', backgroundColor:"#ff6600", borderColor:"#ff6600" }} block>
                  Login
                </Button>
                <br />
                <div style={{color:"grey",textAlign:"center"}}>Dont have an account?<a href="https://lbi-probono.herokuapp.com/signup" style={{color:"#ff6600",paddingLeft:"30px"}}>    Create one</a></div>
              </FormGroup>
            </Form>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  error: state.error
});

export default connect(
  mapStateToProps,
  { login, clearErrors }
)(LoginModal);