import React, { Component, useState } from "react";
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Alert,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { calendar } from "./calendar.png";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { connect } from "react-redux";
import { DateTimePicker } from "react-widgets";
import PropTypes from "prop-types";
import { register } from "../../actions/authActions";
import { clearErrors } from "../../actions/errorActions";

// Moment.locale('en')
// momentLocalizer()

class RegisterComponent extends Component {
  constructor() {
    super();
    this.state = {
      dropdownOpen: false,
      modal: false,
      firstName: "",
      lastName: "",
      dob: new Date(),
      gender: "Select gender",
      dropdownOpenGender: false,
      email: "",
      password: "",
      confirmPassword: "",
      accountType: "Select account",
      address: "",
      city: "",
      province: "",
      Country: "",
      telephoneNumber: "",
      PostalCode: "",
      dontMatch:false,
      msg: null,
    };
    this.toggle = this.toggle.bind(this);
    this.changeValue = this.changeValue.bind(this);
    this.toggleGender = this.toggleGender.bind(this);
    this.changeGender = this.changeGender.bind(this);
this.onChange = this.onChange.bind(this);
  }

  static propTypes = {
    isAuthenticated: PropTypes.bool,
    error: PropTypes.object.isRequired,
    register: PropTypes.func.isRequired,
    clearErrors: PropTypes.func.isRequired,
  };

  changeValue(e) {
    this.setState({ dropDownValue: e.currentTarget.textContent });
  }

  componentDidUpdate(prevProps) {
    const { error, isAuthenticated } = this.props;
    if (error !== prevProps.error) {
      // Check for register error
      if (error.id === "REGISTER_FAIL") {
        this.setState({ msg: error.msg.msg });
      } else {
        this.setState({ msg: null });
      }
    }
    if(isAuthenticated===true){
        this.props.history.push('/userType')
    }
  }

  handleChange = (date) => {
    this.setState({
      dob: date,
    });
  };

 onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    console.log(this.state);
  };
  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen,
    });
  }

  toggleGender() {
    this.setState({
      dropdownOpenGender: !this.state.dropdownOpenGender,
    });
  }

  changeValue(e) {
    this.setState({ accountType: e.currentTarget.textContent });
  }
  changeGender(e) {
    this.setState({ gender: e.currentTarget.textContent });
  }

  onSubmit = (e) => {
    e.preventDefault();

    const {
      firstName,
      lastName,
      dob,
      gender,
      email,
      password,
      confirmPassword,
      accountType,
      address,
      city,
      province,
      Country,
      telephoneNumber,
      PostalCode,
    } = this.state;

    // Create user object
    const newUser = {
      firstName,
      lastName,
      dob,
      gender,
      email,
      password,
      confirmPassword,
      accountType,
      address,
      city,
      province,
      Country,
      telephoneNumber,
      PostalCode,
    };

    // Attempt to register
    this.props.register(newUser);
  };

  render() {
    return (
      <div style={{marginTop:20}}>
        {this.state.msg ? <Alert color="danger">{this.state.msg}</Alert> : null}
        <Form style={{ paddingLeft: "40px" }} onSubmit={this.onSubmit}>
          <FormGroup>
            <div style={{ paddingLeft: "40px" }}>
              <h3>Identification</h3>
              <Input
                type="text"
                name="firstName"
                id="firstName"
                placeholder="First Name"
                style={{ width: 470, borderColor: "#ff6600" }}
                className="mb-3"
                onChange={this.onChange}
              />
              <Input
                type="text"
                name="lastName"
                id="lastName"
                placeholder="Last Name"
                style={{ width: 470, borderColor: "#ff6600" }}
                className="mb-3"
                onChange={this.onChange}
              />
              {/* <Label for='dob'>Date of birth</Label> */}
              <div style={{ display: "flex" }}>
              <label style={{paddingRight:"20px"}}> Date of birth:</label>
                  <DatePicker
                    selected={this.state.dob}
                    onChange={this.handleChange}
                    maxDate={new Date()}
                  />

                {/* <Input
                  type='text'
                  name='dob'
                  id='dob'
                  stsyle={{borderColor: "orange"}}
                  placeholder='Date of birth'
                //   className='mb-3'
                  onChange={this.onChange}
                /> */}

                <Dropdown
                  style={{ paddingLeft: "40px" }}
                  direction="down"
                  isOpen={this.state.dropdownOpenGender}
                  toggle={this.toggleGender}
                >
                  <DropdownToggle caret outline color="warning">
                    {this.state.gender}
                  </DropdownToggle>
                  <DropdownMenu onChange={this.changeGender} name="carType">
                    <DropdownItem onClick={this.changeGender} name="classic">
                      Male
                    </DropdownItem>
                    <DropdownItem onClick={this.changeGender} name="deluxe">
                      Female
                    </DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </div>
            </div>
            <br />
            <div style={{ paddingLeft: "40px" }}>
              <h3>Account Creation</h3>
             
              <Input
                type="text"
                name="email"
                style={{ width: 470, borderColor: "#ff6600" }}
                id="email"
                placeholder="Email"
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Input
                type="password"
                name="password"
                id="password"
                placeholder="Password"
                style={{ width: 470, borderColor: "#ff6600" }}
                className="mb-3"
                onChange={this.onChange}
              />
              {/* {this.state.dontMatch?<div><b style={{color:"red"}}>Passwords dont match!</b></div>:null} */}
              <Input
                type="password"
                name="confirmPassword"
                id="confirmPassword"
                placeholder="Confirm Password"
                style={{ width: 470, borderColor: "#ff6600" }}
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Dropdown
                style={{ color: "orange" }}
                direction="down"
                isOpen={this.state.dropdownOpen}
                toggle={this.toggle}
              >
                <DropdownToggle caret outline color="warning">
                  {this.state.accountType}
                </DropdownToggle>
                <DropdownMenu onChange={this.changeValue} name="carType">
                  <DropdownItem onClick={this.changeValue} name="classic">
                    Athelete
                  </DropdownItem>
                  <DropdownItem onClick={this.changeValue} name="deluxe">
                    Coach
                  </DropdownItem>
                  <DropdownItem onClick={this.changeValue} name="luxury">
                    Coordinator
                  </DropdownItem>
                  <DropdownItem onClick={this.changeValue} name="luxury">
                    Administration
                  </DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </div>
            <br />
            <div style={{ paddingLeft: "40px" }}>
              <h3>Contact Information</h3>
             
              <Input
                type="address"
                name="address"
                id="address"
                style={{ width: 470, borderColor: "#ff6600" }}
                placeholder="Address"
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Input
                type="city"
                name="city"
                id="city"
                style={{ width: 470, borderColor: "#ff6600" }}
                placeholder="City"
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Input
                type="province"
                name="province"
                id="province"
                style={{ width: 470, borderColor: "#ff6600" }}
                placeholder="Province"
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Input
                type="Country"
                name="Country"
                style={{ width: 470, borderColor: "#ff6600" }}
                id="Country"
                placeholder="Country"
                className="mb-3"
                onChange={this.onChange}
              />
              
              <Input
                type="text"
                name="telephoneNumber"
                id="telephoneNumber"
                style={{ width: 470, borderColor: "#ff6600" }}
                placeholder="Telephone Number"
                className="mb-3"
                onChange={this.onChange}
              />
             
              <Input
                type="text"
                name="PostalCode"
                id="PostalCode"
                placeholder="Postal Code"
                style={{ width: 470, borderColor: "#ff6600" }}
                className="mb-3"
                onChange={this.onChange}
              />
            </div>

            <Button
              color="dark"
              style={{
                marginTop: "2rem",
                backgroundColor: "#ff6600",
                borderColor: "#ff6600",
                margin:"auto",
                width:"200px"
              }}
              block
            >
              Submit application
            </Button>
          </FormGroup>
        </Form>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  error: state.error,
});

export default connect(mapStateToProps, { register, clearErrors })(
  RegisterComponent
);
